
.driver-view {
  display: flex;
  flex-direction: column;
  height: 100vh;
}
.top-bar {
  height: 100px;
  text-align: center;
  background: white;
}
.map {
  flex-grow: 1;
  background-color: rgba(0, 0, 0, 0.1);
}
.bottom-bar {
  padding: 20px 0px;
  text-align: center;
  background: white;
}

.admin-view {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: row;
}
.left-view {
  width: 250px;
  padding: 12px;
}
.right-view {
  flex-grow: 1;
}
